import { FC, useEffect, useState } from "react";
import ambulatePage from "./ambulatePage.module.css"
import microphone from "../../assets/Microphone.png"
import microphoneDiactivated from "../../assets/MicrophoneDeactivated.png"
import sound_on from "../../assets/turn_on.mp3"
import sound_off from "../../assets/turn_off.mp3"
import { useNavigate, useParams } from "react-router";
import { Packer } from "docx";
import { setDoc } from "../../utils/docCreator";
import { useAppDispatch, useAppSelector } from "../../hooks/useTypedSelector";
import { setPatient } from "../../store/reducers/ambulateReducer";
import arrow from "../../assets/ArrowShift.png"
import { saveAs } from "file-saver";
import Modal from "../Modal/Modal";
import arrowWide from "../../assets/Arrow.png"
import arrowWideRotate from "../../assets/ArrowRotate.png"
import enter from "../../assets/Space.png"
import arrowRotateLeft from "../../assets/ArrowRotateLeft.png"

export const inputNames = [
    {name: "Ф.И.О. пациента", placeholder: "Иванов Иван Иванович", height: "60px"},
    {name: "Дата рождения", placeholder: "19.09.1999", height: "60px"},
    {name: "Жалобы", placeholder: "Боль за грудиной, в области сердца, одышка, учащенное сердцебиение, перебои в работе сердца, отеки нижних конечностей, лица, головную боль, головокружение", height: "160px"},
    {name: "Анамнез заболевания", placeholder: "Боль за грудиной, в области сердца, одышка, учащенное сердцебиение, перебои в работе сердца, отеки нижних конечностей, лица, головную боль, головокружение", height: "160px"},
    {name: "Аллергологический анамнез", placeholder: "Не отягощён, общее состояние удовлетворительное, положение тела активное", height: "100px"},
    {name: "Телосложение", placeholder: "Нормостеническое", height: "60px"},
    {name: "Рост", placeholder: "180 см", height: "60px"},
    {name: "Вес", placeholder: "100 кг", height: "60px"},
    {name: "ИМТ", placeholder: "56", height: "60px"},
    {name: "Температура тела", placeholder: "36,6 °С", height: "60px"},
    {name: "Кожные покровы", placeholder: "Окраска бледно-розовая, кожа влажная, рубцы, сыпь, сосудистые звездочки", height: "100px"},
    {name: "Слизистая ротовой полости", placeholder: "Розовая, имеются язвы, гиперемия", height: "60px"},
    {name: "Конъюнктива", placeholder: "Бледно-розовая, гиперемирована, желтушна, поверхность гладкая", height: "100px"},
    {name: "Подкожно-жировая клетчатка", placeholder: "Выражена избыточно", height: "60px"},
    {name: "Подкожные лимфатические узлы", placeholder: "Не увеличены", height: "60px"},
    {name: "Сердечно-сосудистая система", placeholder: "Тоны ясные, громкие. Шумы: систолический (функциональный, органический), локализуется на верхушке, справа от грудины", height: "150px"},
    {name: "Артериальное давление", placeholder: "120/70 мм.рт.ст", height: "60px"},
    {name: "ЧСС", placeholder: "80 в 1 минуту", height: "60px"},
    {name: "Дыхательная система", placeholder: "Одышка отсутствует, инспираторная, экспираторная, возникает при беге. Частота дыхательных движений: 17 в 1 минуту. Перкуторный звук ясный лёгочный, притуплённый, укороченный, тимпанический, коробочный. Границы лёгких: одностороннее, двустороннее опущение, смещение вверх нижних границ. В лёгких при аускультации дыхание везикулярное, жёсткое, ослаблено слева, справа, в верхних, нижних отделах, по передней, задней, боковой поверхности. Хрипы отсутствуют, единичные, множественные, мелко- средне- крупнопузырчатые, сухие, влажные, свистящие, крепитирующие, застойного характера слева, справа, по передней, задней, боковой поверхности, в верхних, средних, нижних отделах. Мокрота", height: "690px"},
    {name: "Пищеварительная система", placeholder: "Запах изо рта , Язык влажный, сухой, чистый, обложен налётом. Живот увеличен за счёт п/жировой клетчатки, отёков, грыжевых выпячиваний  при пальпации мягкий, безболезненный, болезненный. Симптом раздражения брюшины есть, нет. Печень по краю рёберной дуги, увеличена болезненная, плотная, мягкая, поверхность гладкая, бугристая. Селезёнка нормальная, увеличена, болезненная. Перистальтика не нарушена, нарушена. Дефекация 2 раза в сутки/неделю, безболезненная, болезненная, кал оформленный, жидкий, коричневого цвета, без примеси слизи и крови.", height: "530px"},
    {name: "Мочевыделительная система", placeholder: "Симптом поколачивания по пояснице: отрицательный, положительный слева, справа, с обеих сторон. Мочеиспускание 4-6 раз в сутки, безболезненное, болезненное, учащённое, редкое, никтурия, олигурия, анурия, моча светло-соломенного цвета", height: "250px"},
    {name: "Диагноз", placeholder: "Эрозивный эзофагит (стадия В)", height: "60px"},
    {name: "План обследования", placeholder: "Консультации специалистов, ЭКГ, УЗИ, ФГ, ОАМ, ОАК, глюкоза крови, биохимический анализ крови", height: "100px"},
    {name: "План лечения", placeholder: "Немедикаментозное лечение: диета №15, физические тренировки, оксигенотерапия. Медикаментозное лечение: Пиперациллин 1 раз в сут., АЦЦ по 200 мг 2 раза в сут., беродуал по 2 вдоха 2 раза в сут.", height: "200px"},
]

const AmbulatePage: FC = () => {

    const dispatch = useAppDispatch()
    const { data } = useAppSelector(state => state.ambulate)

    const [value, setValue] = useState<any>({strings:[]})
    const [selectedField, setSelectedField] = useState(1)
    const [micActive, setMicActive] = useState(false)
    const [clear, setClear] = useState(false)
    const [modalActive, setModalActive] = useState(false)

    const turn_on = new Audio(sound_on)
    const turn_off = new Audio(sound_off)

    const navigate = useNavigate()
    const currentPath = window.location.pathname;
    const grammar = "#JSGF V1.0"
    const speechRecognitionList = new webkitSpeechGrammarList
    speechRecognitionList.addFromString(grammar, 1);
    
    window.SpeechRecognition = window.webkitSpeechRecognition
    const recognition = new SpeechRecognition()
    recognition.continuous = true
    recognition.lang = "ru"
    recognition.grammars = speechRecognitionList

    // eslint-disable-next-line
    const handlerKeyPress = (e: any) => {
        if (e.code === "Enter" && selectedField !== 0){
            e.preventDefault()
            if (micActive === true){
                turn_off.play()
                setMicActive(!micActive)
            }else{
                turn_on.play()
                setMicActive(!micActive)
            }
        }else if (e.code === "ArrowDown"){
            e.preventDefault()
            if (micActive === true){
                return
            }
            let accessedElement = null
            if (selectedField < inputNames.length){
                accessedElement = document.querySelector(`[data-key="${selectedField + 1}"]`)
                setSelectedField(prevState => prevState + 1)
            }else{
                accessedElement = document.querySelector(`[data-key="${selectedField}"]`)
            }
            if (accessedElement != null){
                let headerOffset = 180;
                let elementPosition = accessedElement.getBoundingClientRect().top;
                let offsetPosition = elementPosition + window.pageYOffset - headerOffset;
            
                window.scrollTo({
                    top: offsetPosition,
                });
            }   
        }else if (e.code === "ArrowUp"){
            e.preventDefault()
            if (micActive === true){
                return
            }
            let accessedElement = null
            if (selectedField === 1){
                accessedElement = document.querySelector(`[data-key="${selectedField}"]`)
            }else if (selectedField === 0){
                accessedElement = document.querySelector(`[data-key="${selectedField + 1}"]`)
                setSelectedField(prevState => prevState + 1)
            }
            else{
                accessedElement = document.querySelector(`[data-key="${selectedField - 1}"]`)
                setSelectedField(prevState => prevState - 1)
            }
            if (accessedElement != null){
                let headerOffset = 180;
                let elementPosition = accessedElement.getBoundingClientRect().top;
                let offsetPosition = elementPosition + window.pageYOffset - headerOffset;
            
                window.scrollTo({
                    top: offsetPosition,
                });
            }
        }else if(e.code === "ArrowLeft"){
            e.preventDefault()
            if (micActive === true){
                return
            }
            navigate("/")
        }else if(e.code === "KeyP"){
            e.preventDefault()
            if (micActive === true){
                return
            }
            Packer.toBlob(setDoc(data)).then(res => saveAs(res, "test.docx"))
        }else if(e.code === "ArrowRight"){
            e.preventDefault()
            if (micActive === true){
                return
            }
            if (currentPath === "/tvoi_hod/ambulatePage"){
                setClear(true)
            }
        }else if(e.code === "ShiftRight"){
            e.preventDefault()
            if (micActive === true){
                return
            }
            setModalActive(!modalActive)
        }
    }

    useEffect(() => {
        if (clear === true){
            const newValue = value.strings.slice(0, value.strings.length)
            for (let i = 0; i < value.strings.length; i++){
                newValue[i] = ""
            }
            setValue({strings:newValue})
            dispatch(setPatient({data: newValue}))
        }
        setClear(false)
    },[clear])

    useEffect(() => {
        let currentArea:any = null
        let elem = null
        if (selectedField !== 0){
            elem = document.querySelector(`[parent-key="${selectedField}"]`)
            currentArea = elem?.querySelector('textarea')
        }
        if (currentArea != null){
            currentArea.style.height = inputNames[selectedField - 1].height
            currentArea.style.padding_top = '0px'
            currentArea.style.padding_bottom = '0px'
            currentArea.style.height = `${currentArea.scrollHeight}px`
        }
        // eslint-disable-next-line
    },[value])

    useEffect(() => {
        let element = document.querySelector(`[parent-key="${selectedField}"]`)
        let currentArea = element?.querySelector('textarea')
        if (selectedField !== 0){
            try{
                recognition.addEventListener('result', (e) => { 
                    const transcript = Array.from(e.results) 
                        .map(result => result[0]) 
                        .map(result => result.transcript) 
                        .join('') 
                        const newValue = value.strings.slice(0, value.strings.length)
                        if (value.strings[selectedField] !== "" && currentArea?.selectionStart && data[selectedField] !== undefined){
                            newValue[selectedField] = data[selectedField].slice(0,currentArea?.selectionStart + 1) + transcript.slice(0,1).toLowerCase() + transcript.slice(1,transcript.length)+ " " + data[selectedField].slice(currentArea?.selectionStart + 1, data[selectedField].length)
                            setValue({strings:newValue})
                            dispatch(setPatient({data: newValue}))
                        }
                        else if (value.strings[selectedField] !== "" && currentArea?.selectionStart && data[selectedField] === undefined){
                            newValue[selectedField] = transcript.slice(0,1).toUpperCase() + transcript.slice(1,transcript.length) + " "
                            setValue({strings:newValue})
                            dispatch(setPatient({data: newValue}))
                        }
                        else{
                            newValue[selectedField] = transcript.slice(0,1).toUpperCase() + transcript.slice(1,transcript.length) + " "
                            setValue({strings:newValue})
                            dispatch(setPatient({data: newValue}))
                        } 
                })
            }catch (e){

            }

            if (micActive === false){
                try{
                    recognition.start()
                }catch(e){}
                recognition.stop()
            }else{
                recognition.start()
            }
        }
        // eslint-disable-next-line
    },[micActive])    

    useEffect(() => {
        document.addEventListener("keydown", handlerKeyPress);
        return () => {
            document.removeEventListener("keydown", handlerKeyPress);
        }
    },[handlerKeyPress]);

    const handleEdit = (e:any) => {
        let element = document.querySelector(`[parent-key="${selectedField}"]`)
        let currentArea = element?.querySelector('textarea')
        if (currentArea?.selectionStart !== undefined){
            const newValue = value.strings.slice(0, value.strings.length)
            newValue[selectedField] = e.target.value
            setValue({strings:newValue})
            dispatch(setPatient({data: newValue}))
        } 
    }

    return(
        <div tabIndex={1} className={ambulatePage.body}>
            <div className={ambulatePage.header}>
                <p className={ambulatePage.state}>
                    Состояние:
                </p>
                {micActive
                ?
                <div className={ambulatePage.stateMic}>
                    <img src={microphone} alt="Not Found"/>
                    <p className={ambulatePage.micActive}>
                        Идет запись
                    </p>
                </div>
                :
                <div className={ambulatePage.stateMic}>
                    <img src={microphoneDiactivated} alt="Not Found"/>
                    <p className={ambulatePage.micIgnore}>
                        Режим ожидания
                    </p>
                </div>
                }
                <div className={ambulatePage.advice}>
                    Для вызова&nbsp;<span style={{fontWeight:"bold"}}>ПОДСКАЗКИ</span>&nbsp;нажмите &nbsp;<div className={ambulatePage.imgDiv}><img src={arrow} alt="Not Found"/>&nbsp;&nbsp;SHIFT</div>
                </div>
            </div>
            <div className={ambulatePage.section}>
                {inputNames.map((item, index) => 
                <div key={index} parent-key={index + 1} className={ambulatePage.section_item}>
                    <div>
                        <p data-key={index + 1} className={index + 1 === selectedField ? `${ambulatePage.section_item_field} ${ambulatePage.section_item_field_active}` : ambulatePage.section_item_field}>
                        {item.name}
                        </p>
                    </div>
                    <textarea placeholder={index + 1 === selectedField ? item.placeholder : ""} disabled={index + 1 === selectedField ? false : true} className={ambulatePage.section_item_textarea} style={{height: item.height, resize:"none"}} value={ data[index + 1] !== "" ? data[index + 1] : ""} onChange={(e) => handleEdit(e)}/>
                </div>
                )}
            </div>
            <Modal active={modalActive} setActive={setModalActive}>
                <div className={ambulatePage.modal}>
                    <div className={ambulatePage.advice_item}>
                        Для <span className={ambulatePage.advice_span}>&nbsp;ПЕРЕМЕЩЕНИЯ МЕЖДУ СТРОКАМИ&nbsp;</span> используйте клавиши&nbsp;<div className={ambulatePage.imgDivArrow}><img src={arrowWide} alt="Not Found"/></div>&nbsp;и&nbsp;<div className={ambulatePage.imgDivArrow}><img src={arrowWideRotate} alt="Not Found"/></div>
                    </div>
                    <div className={ambulatePage.advice_item}>
                        Для <span className={ambulatePage.advice_span}>&nbsp;НАЧАЛА/ОСТАНОВКИ&nbsp;</span> записи голоса нажмите клавишу&nbsp;<div className={ambulatePage.enterImgDiv}><img src={enter} alt="Not Found"/>&nbsp;&nbsp;ENTER</div>
                    </div>
                    <div className={ambulatePage.advice_item}>
                        Для <span className={ambulatePage.advice_span}>&nbsp;ПЕЧАТИ&nbsp;</span> листа осмотра нажмите клавишу&nbsp;<div className={ambulatePage.imgDivP}>P</div>
                    </div>
                    <div className={ambulatePage.advice_item}>
                        Для <span className={ambulatePage.advice_span}>&nbsp;ВОЗВРАТА К ВЫБОРУ ШАБЛОНА&nbsp;</span> нажмите клавишу&nbsp;<div className={ambulatePage.imgDivArrow}><img src={arrowRotateLeft} alt="Not Found"/></div>
                    </div>
                    <div className={ambulatePage.advice_item}>
                        Чтобы <span className={ambulatePage.advice_span}>&nbsp;СКРЫТЬ ПОДСКАЗКУ&nbsp;</span> нажмите клавишу&nbsp;<div className={ambulatePage.shiftImgDiv}><img src={arrowWide} alt="Not Found"/>&nbsp;SHIFT</div>
                    </div>
                </div>
            </Modal>
        </div>
    )
}

export default AmbulatePage;