import { FC, ReactNode } from "react"
import modal from "./modal.module.css"

type T = {
    active: boolean,
    setActive: Function,
    children: ReactNode
}

const Modal:FC<T> = ({active, setActive, children}) => {

    return(
        <div className={active ? modal.modal_active : modal.modal} onClick={() => setActive(false)}>
            <div className={active ? modal.modal_contentActive : modal.modal_content} onClick={(e) => e.stopPropagation()}>
                {children}
            </div>
        </div>
    )
}

export default Modal