import { AlignmentType, BorderStyle, dateTimeValue, Document, Footer, FootnoteReferenceRun, HeadingLevel, Packer, Paragraph, Table, TableCell, TableRow, TextRun } from "docx"
import { saveAs } from "file-saver"
import { useAppSelector } from "../hooks/useTypedSelector";

const today = new Date()

const createSignatureLine = () => {
    return new Paragraph({
        children: [
            new TextRun({ text: '______________________', font: 'Times New Roman', size: 24 })
        ],
        alignment: AlignmentType.CENTER
    });
};

const createCenteredText = (text:any) => {
    return new Paragraph({
        children: [
            new TextRun({ text, font: 'Times New Roman', size: 24 })
        ],
        alignment: AlignmentType.CENTER
    });
};


