import { FC, useEffect, useState } from "react"
import mainPage from "./mainPage.module.css"
import arrow from "../../assets/Arrow.png"
import space from "../../assets/Space.png"
import { Link, useNavigate} from "react-router-dom"

const MainPage:FC = () => {

  const [selectedTemplate, setSelectedTemplate] = useState(1)

  const templates = [
  {name: "Первичный осмотр (амбулатория)", component: "/ambulatePage", id: 1},
  {name: "Стационарный осмотр (в разработке)", component: "/", id: 2},
  ]

  let navigate = useNavigate();

  const clickHandler = (e:any) => {
    if (e.code === "Enter"){
      e.preventDefault()
      navigate(templates[selectedTemplate - 1].component)
    }else if(e.code === "ArrowDown"){
      e.preventDefault()
      if (selectedTemplate === templates.length){
        return
      }
      setSelectedTemplate(prev => prev + 1)
    }else if(e.code === "ArrowUp"){
      e.preventDefault()
      if (selectedTemplate === 1){
        return
      }
      setSelectedTemplate(prev => prev - 1)
    }
  }

  useEffect(() => {
    document.addEventListener("keydown", clickHandler);
    return () => document.removeEventListener("keydown", clickHandler);
  }, [clickHandler]);

  return (
    <div className={mainPage.body}>
      <div className={mainPage.header}>
        Выберите шаблон листа осмотра:
      </div>
      <div className={mainPage.content}>
        {templates.map((item, index) => 
          <div key={index + 1} className={index + 1 === selectedTemplate ? mainPage.content_item_active : mainPage.content_item}>
            <Link to={item.component} className={mainPage.content_link}>
                {item.name}
            </Link>
          </div>
        )}
      </div>
      <div className={mainPage.footer}>
        <div className={mainPage.footer_item}>
          Для <span className={mainPage.footer_span}>&nbsp;ПЕРЕМЕЩЕНИЯ МЕЖДУ СТРОКАМИ&nbsp;</span> используйте клавиши &nbsp;<div className={mainPage.arrowImgDiv}><img src={arrow} alt="Not Found"/></div>&nbsp; и &nbsp;<div className={mainPage.arrowImgDiv}><img src={arrow} className={mainPage.arrowTransformed} alt="Not Found"/></div>
        </div>
        <div className={mainPage.footer_item}>
          Для <span className={mainPage.footer_span}>&nbsp;ВЫБОРА СТРОКИ&nbsp;</span> нажмите клавишу &nbsp;<div className={mainPage.enterImgDiv}><img src={space} alt="Not Found"/>&nbsp;&nbsp;ENTER</div>
        </div>
      </div>
    </div>
  );
}

export default MainPage;
